import * as lodash from '@microsoft/sp-lodash-subset';

/**
 * Get's the path name from an absolute url.
 *
 */
export function getPathNameFromAbsoluteUrl(url: string): string {
  const anchorElement: HTMLAnchorElement = document.createElement('a');
  anchorElement.href = url;
  let pathName: string = anchorElement.pathname;

  // IE Fix where pathname doesn't return first slash
  if (pathName.charAt(0) !== '/') {
    pathName = '/' + pathName;
  }
  return pathName;
}

/**
 * Combines any number of URL paths.
 */
export function combineURLPaths(...url: string[]): string {
  let fullURLPath: string = '';
  for (let i: number = 0; i < url.length; i++) {
    fullURLPath += lodash.trimEnd(url[i], '/');
  }
  return fullURLPath;
}
